import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Result from '../views/Result.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title:'Foodstagram | All The Recipes in One Place'
    }
  },
  {
    path: '/Result/:id',
    name: 'Result',
    component: Result,
    meta: {
      title:'Foodstagram | Recipe'
    }
  }
];



const router = new VueRouter({
  routes
})

export default router
