<template>
  <div class="home" >
    <router-view/>
    <Recipe :title="postTitle" :description="postDescription" :likes="postId" :thumbnail="postThumbnail" :source="postSource" :ingredients="postIngredients" :steps="postSteps"/>
  </div>
</template>

<script>
import Recipe from '@/components/Recipe.vue'
import axios from 'axios'
export default {
  name: 'Result',
  components: {
    Recipe
  },
  data: function(){
    return {
      postId: this.$route.params.id,
      postTitle: null,
      postDescription: null,
      postThumbnail:null,
      postSource: null,
      postIngredients: null,
      postSteps:null,
      loading: false,
    };
  },

  mounted(){
    this.PostInfo();
  },

  methods:{
    async PostInfo(){
      const apiKey = "c8a9f32889154df290af379a63ceceee";
      let id = this.$route.params.id;
      this.loading = true;
      try{
        let res = await axios({
          url: 'https://api.spoonacular.com/recipes/informationBulk?apiKey='+apiKey+'&ids='+id,
          method: "GET",
        });
        let recipe = res.data[0];
        this.loading = false;
        this.postTitle = recipe.title;
        this.postDescription = recipe.summary;
        this.postSource = recipe.sourceUrl;
        this.postThumbnail = recipe.image;
        this.postIngredients = recipe.extendedIngredients;
        this.postSteps = recipe.instructions;
      } catch(error){
        this.loading = false;
      }
    },
  }
 }
</script>
