<template>
  <div class="bodybox">
    <div id="result" class="">
      <div class="container2" id="recipe">
        <div class="head">
          <div class="logo2">
            <label for="logo">EASY & HEALTHY RECIPES</label>
            <img src="../assets/unnamed2.png" id="logo" alt="">
            <label for="logo">FOR THE FOOD YOU CRAVE</label>
          </div>
          <div class="navbar">
            <nav id="navbar">
              <router-link to="/" id="home">Home</router-link>
              <p id="rand2" v-on:click="RandomPosts()">Get Random Recipe</p>
              <a href="https://jcanotorr06.github.io/Portfolio/" target="_blank">About Me</a>
            </nav>
          </div>
        </div>
        <div class="content">
          <div class="left">
            <div class="thumbnail">
              <img :src="thumbnail" alt="" id="thumbnail">
            </div>
            <div class="ingredients">
              <table>
                <thead>
                  <tr>
                    <th>Ingredients</th>
                    <th></th>
                  </tr>
                </thead>
                <tbody v-for="ingredient in ingredients"  :key="ingredient.id">
                  <tr>
                    <td>{{ ingredient.name }}</td>
                    <td>{{ingredient.amount + ' ' + ingredient.unit}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="source">
              <h2>Source:</h2>
              <a :href="source" id="source">{{ source }}</a>
            </div>
          </div>

          <div class="recipe">
            <div class="recipe-title">
              <h1 id="rec-title">{{ title }}</h1>
            </div>
            <div class="extra">
              <div class="likes">
                <label for="likes">Likes:</label>
                <i id="likes">{{ likes }}</i>
              </div>
            </div>
            <div class="description">
              <p id="desc" v-html="description">{{ description }}</p>
            </div>
            <div class="method">
              <h1 id="method">Instructions</h1>
              <div class="instruc">
                <p v-html="steps" id="steps">{{steps}}</p>
              </div>
            </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'Recipe',
  props: {
    title: String,
    description: String,
    likes: {},
    thumbnail: String,
    source: String,
    ingredients: {},
    steps: String,
  },
  methods:{
    async RandomPosts() {
      const apiKey = "c8a9f32889154df290af379a63ceceee";
      this.loading = true;
       try {
        let res = await axios({
          url: 'https://api.spoonacular.com/recipes/random?apiKey='+apiKey+'&number=1',
          method: "GET",
        });
        let recipe = res.data.recipes[0];
        this.loading = false;
        this.$router.push({name:'Result', params: {id:recipe.id}});
        this.$router.go();
        console.log(recipe.id);
       } catch(error){
         this.loading = false;
         console.log('Error')
       }
  }
}
}
</script>

