<template>
  <div class="bodybox">
    <div class="container" id="container">
      <div class="logo">
        <img src="../assets/unnamed2.png" alt="" >
      </div>
      <div class="title">
        <h1>Feeling Hungry?</h1>
      </div>
      <div class="finder">
        <input type="text" name="fincer" id="finder" placeholder="Find a recipe" v-on:keyup.enter="SearchPost($event.target.value)" v-model="query"> <button type="button" name="search" id="search" v-on:click="SearchPost(query)">Search</button>
      </div>
      <div class="random">
        <div class="subtitle">
          <h2>Get a random meal by clicking in the button below</h2>
        </div>
        <div class="rand-but">
          <button type="button" name="button" id="rand" v-on:click="RandomPosts()">Get a meal</button>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import axios from "axios";
export default {
  name: 'Landing',
  data() {
    return {
      posts: null,
      loading: false,
      query:'',
    };
  },
  methods: {
    async RandomPosts() {
      const apiKey = "c8a9f32889154df290af379a63ceceee";
      this.loading = true;
       try {
        let res = await axios({
          url: 'https://api.spoonacular.com/recipes/random?apiKey='+apiKey+'&number=1',
          method: "GET",
        });
        let recipe = res.data.recipes[0];
        this.loading = false;
        this.$router.push({name:'Result', params: {id:recipe.id}});
        console.log(recipe.id);
       } catch(error){
         this.loading = false;
         console.log('Error')
       }
    },
    async SearchPost(q) {
      const apiKey = "c8a9f32889154df290af379a63ceceee";
      this.loading = true;
       try {
        let res = await axios({
          url: 'https://api.spoonacular.com/recipes/search?apiKey='+apiKey+'&number=1&query='+q,
          method: "GET",
        });
        let recipe = res.data.results[0];
        console.log(recipe.id);
        this.loading = false;
        this.$router.push({name:'Result', params: {id:recipe.id}});
       } catch(error){
         this.loading = false;
         console.log('Error')
       }

     },
  }
};
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap');

*{
  box-sizing: border-box;
}

.bodybox{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 90vh;
  text-align: center;
  font-family: "Poppins", sans-serif;
  margin: 0;
}


.title {
  margin-top: -2rem;
  font-size: 2rem;
  margin-bottom: -1.5rem;
}

.finder{
}

.finder input{
  width: 80%;
  padding: 8px;
  font-size: 1.4rem;
  border-radius: 5px;
  box-sizing: border-box;
}

.finder button{
  width: 15%;
  padding: 8px;
  font-size: 1.4rem;
  border-radius: 5px;
  box-sizing: border-box;
  background-color: #b08155;
  color: white;
  cursor: pointer;
}

.finder button:hover{
  background-color: #643e19;
}

.subtitle{
  padding: 1.5rem;
}


.rand-but button{
  width: 30%;
  font-weight: bold;
  font-size: 1.5rem;
  border: 2px solid #000;
  min-height: 6vh;
  border-radius: 8px;
  background-color: #b08155;
  color: white;
  cursor: pointer;
}

.rand-but button:hover{
  background-color: #643e19;
}

.empty{
  display: none;
}

#image img{
  max-width: 50vw;
  max-height: 50vh;
}

</style>
